export const myDOM = {
  /**
   *  class
   */
  hasClass (el, className) {
    let reg = new RegExp('(^|\\s)' + className + '(\\s|$)')
    return reg.test(el.className)
  },

  /**
   *  class
   */
  addClass (el, className) {
    if (this.hasClass(el, className)) {
      return
    }
    let newClass = el.className.split(' ')
    newClass.push(className)
    el.className = newClass.join(' ')
  },

  /**
   * /  data-${name}=val
   *  val  `data-${name}`  val
   *  val  `data-${name}` 
   */
  customAttribute (el, name, val) {
    if (val) {
      return el.setAttribute(`data-${name}`, val)
    } else {
      return el.getAttribute(`data-${name}`)
    }
  },

  /**
   * 
   * 
   * 
   * 
   * 1
   * 
   * 
   *
   * document.addEventListener('scroll', debounce(function () {
   *   console.log('hello');
   * }, 1000));
   *
   * @param  {Function} fn    
   * @param  {number}   delay 
   */
  debounce (fn, delay) {
    let timer = null

    return function(...args) {
      if (timer) {
        clearTimeout(timer)
      }

      timer = setTimeout(() => {
        fn.apply(this, args)
      }, delay)
    }
  }
}

export const myTime = {
  /**
   * (:)
   * 61 -> 01:01
   */
  format(timestamp) {
    timestamp = Math.floor(timestamp)

    let minute = (Math.floor(timestamp / 60)).toString().padStart(2, '0')
    let second = (timestamp % 60).toString().padStart(2, '0')
    return `${minute}:${second}`
  }
}

export const myNumber = {
  /**
   *  [min, max] 
   * @param  {number} min 
   * @param  {number} max 
   */
  getRandom (min, max) {
    return Math.floor(Math.random() * (max - min + 1) + min)
  }
}

export const myArray = {
  /**
   * 
   * @param  {array} arr 
   * @return {array}     
   */
  cloneArr (arr) {
    //  copy
    // slice(start,end) 
    return arr.slice(0)
  },

  /**
   * 
   * @param  {Array} arr 
   * @param  {boolean} flag 
   * @return {Array}     
   */
  shuffle (arr, flag = false) {
    let newArr = []
    flag ? (newArr = arr) : (newArr = this.cloneArr(arr))

    for (let i = 0; i < newArr.length; i++) {
      let j = myNumber.getRandom(0, i)
      let temp = newArr[i]
      newArr[i] = newArr[j]
      newArr[j] = temp
    }
    return newArr
  },

  /**
   * 
   * @param  {array} arr 
   * @return {[type]}    
   */
  randomMember (arr) {
    return arr[Math.floor(Math.random() * arr.length)]
  }
}

