import storage from 'good-storage'

const SEARCH_KEY = '__search__'
// const SEARCH_MAX_LEN = 15

/**
 *  arr[0] 
 * @param  {[type]} arr   
 * @param  {[type]} val   
 * @param  {fn} compareFn 
 * @param  {num} maxLen   
 * @return {[type]}           [description]
 */
function insertArr(arr, val, compareFn, maxLen = 15) {
  let index = arr.findIndex(compareFn)
  if (index === 0) {
    return
  }
  if (index > 0) {
    arr.splice(index, 1)
  }
  // unshift() 
  arr.unshift(val)
  if (maxLen && arr.length > maxLen) {
    // pop() 
    arr.pop()
  }
}

function delArr(arr, compareFn) {
  let index = arr.findIndex(compareFn)
  if (index > -1) {
    arr.splice(index, 1)
  }
}

// 
export function getStorage() {
  return storage.get(SEARCH_KEY, [])
}

// 
export function localSave(query) {
  //  localstorage 
  let searches = storage.get(SEARCH_KEY, [])

  insertArr(searches, query, (item) => {
    return item === query
  }, 15)

  storage.set(SEARCH_KEY, searches)
  return searches
}

// 
export function localDel(query) {
  //  localstorage 
  let searches = storage.get(SEARCH_KEY, [])

  delArr(searches, (item) => {
    return item === query
  })

  storage.set(SEARCH_KEY, searches)
  return searches
}

// 
export function localClear(query) {
  storage.remove(SEARCH_KEY)
  return []
}

//  localstorage
const PLAY_KEY = '__play__'

// 
export function getPlayStorage() {
  return storage.get(PLAY_KEY, [])
}

// 
export function savePlay(song) {
  //  localstorage 
  let songs = storage.get(PLAY_KEY, [])

  insertArr(songs, song, (item) => {
    return item.id === song.id
  }, 50)

  storage.set(PLAY_KEY, songs)
  return songs
}

//  localstorage
const FAVORITE_KEY = '__favorite__'

// 
export function getFavorite() {
  return storage.get(FAVORITE_KEY, [])
}

// 
export function saveFavorite(song) {
  //  localstorage 
  let songs = storage.get(FAVORITE_KEY, [])

  insertArr(songs, song, (item) => {
    return item.id === song.id
  }, 50)

  storage.set(FAVORITE_KEY, songs)
  return songs
}

// 
export function delFavorite(song) {
  //  localstorage 
  let songs = storage.get(FAVORITE_KEY, [])

  delArr(songs, (item) => {
    return item.id === song.id
  })

  storage.set(FAVORITE_KEY, songs)
  return songs
}



