/**
 * 
 * vuex state.js -> mutations-type.js -> mutations.js -> getters.js
 */
import { getStorage, getPlayStorage, getFavorite } from '@/common/js/store.js'

const playMode = {
  sequence: 0, // 
  loop: 1, // 
  random: 2 // 
}

const state = {
  // 
  singer: {},
  // 
  playing: false,
  // 
  fullScreen: false,
  // 
  playlist: [],
  // 
  sequenceList: [],
  // 
  mode: playMode.sequence,
  // 
  currentIndex: -1,
  // 
  songlist: {},
  // 
  rankList: {},
  // 
  searchHistory: getStorage(),
  // 
  playHistory: getPlayStorage(),
  // 
  favoriteList: getFavorite()
}

export default state



